import React from "react";

const StudentScatterChartBetaCompanyLazy = React.lazy(() => import("./StudentScatterChartBetaCompany.js"))

export default function StudentScatterChartBetaCompanyLayout(props) {
    const isSSR = typeof window === "undefined"

    if (!isSSR) { return <React.Suspense fallback={<div />}><StudentScatterChartBetaCompanyLazy /></React.Suspense> }

    return <div>Student Scatter Chart Beta Company Layout</div>
}

