import React from 'react';
import { useSelector, useDispatch } from "react-redux";
import { CSVReader } from 'react-papaparse'
import firebase from "gatsby-plugin-firebase"

import { selectApplication } from "../application/applicationSlice";
import { createDataItem, empty_data, getDataItems, saveDataSuccess } from "./dataSlice";

import Button from "react-bootstrap/Button";
import ButtonGroup from "react-bootstrap/ButtonGroup";
import Navbar from "react-bootstrap/Navbar";


function csvColumnTitles(array) {
  let columnTitlesObject = {};
  array.map((item, index) => {
    let keyString = item.replace(/\W/g, '');
    keyString = keyString.toLowerCase();
    //console.log(keyString);
    columnTitlesObject[keyString] = index;

  })
  return columnTitlesObject
}

function csvFinamQuotesToObject(data) {
  let csvDictObject = csvColumnTitles(data[0].data);
  // console.log(csvDictObject);
  let quotesObject = {};
  let ticker = data[1].data[csvDictObject.ticker];
  let per = data[1].data[csvDictObject.per];
  // console.log(ticker, per);
  //console.log(quotesObject);
  data.map((item, index) => {
    if (index > 0) {
      let currentQuote = {};
      Object.keys(csvDictObject).map(key => {
        currentQuote[key] = item.data[csvDictObject[key]]
      })

      if ((!!currentQuote?.ticker) && (!!currentQuote?.per) && (!!currentQuote.date)) {
        //    console.log(currentQuote);
        let newQuote = {};
        newQuote[currentQuote.date] = currentQuote;
        quotesObject = { ...quotesObject, ...newQuote }
      }
    }
  })
  return { quotesObject, ticker, per }
}

function fire_save(ticker_per_object, email) {
  let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");  
  var updates = {};
  updates['/moex/' + ticker_per_object.ticker + '/' + ticker_per_object.per] = ticker_per_object.quotesObject;
  updates['/usersCraft/' + userEmail + '/database/moex/' + ticker_per_object.ticker + '/' + ticker_per_object.per] = ticker_per_object.quotesObject;
  return firebase.database().ref().update(updates);
}

const buttonRef = React.createRef()

export default function ImportCSVSpreadsheet(props) {
  const email = useSelector(selectApplication).email;
  const dispatch = useDispatch();

  function handleOpenDialog(e) {
    // Note that the ref is set async, so it might be null at some point 
    if (buttonRef.current) {
      buttonRef.current.open(e)
    }
  }

  function handleOnFileLoad(data) {
    let ticker_per_object = csvFinamQuotesToObject(data);
   
  //  dispatch(getDataItems());
    Object.keys(ticker_per_object.quotesObject).forEach(element => {
    //  console.log(ticker_per_object.quotesObject[element]);
      // let year = element.substring(0, 4);
      // let month = element.substring(4, 6);
      // let day = element.substring(6, 8);
      // let date = + new Date(year, month - 1, day);
      dispatch(
      createDataItem({...ticker_per_object.quotesObject[element],
        id: ticker_per_object.ticker + '_'+ ticker_per_object.per+'_'+element,
        quotedate: element,
        datatype: 'quote',
        deleted: false,
        date: new Intl.DateTimeFormat("ru", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "numeric",
          minute: "numeric",
        }).format(new Date()), //Date().toJSON()
      })
    );  
    });   
  //  dispatch(saveDataSuccess()); 
  fire_save(ticker_per_object, email);
  }

  function handleOnError(err, file, inputElem, reason) {
    console.log(err)
  }

  function handleOnRemoveFile(data) {
    console.log('---------------------------')
    console.log(data)
    console.log('---------------------------')
  }

  function handleRemoveFile(e) {
    // Note that the ref is set async, so it might be null at some point
    if (buttonRef.current) {
      buttonRef.current.removeFile(e)
    }
  }


  return <>
    <CSVReader
      ref={buttonRef}
      onFileLoad={handleOnFileLoad}
      onError={handleOnError}
      noClick
      noDrag
      onRemoveFile={handleOnRemoveFile}
     
    >
      {({ file }) => (
        <Navbar bg="light" expand="sm">
          <ButtonGroup aria-label="Posts Buttons" size="sm">
      <Button variant="outline-secondary" onClick={handleOpenDialog} size="sm">{props.openButton}</Button>
            {/* <Button variant="outline-secondary" onClick={handleRemoveFile} size="sm"> </Button> */}
          </ButtonGroup>        
            <Navbar.Brand style={{minWidth: '150px', marginLeft: '10px'}}><small>{file && file.name}</small></Navbar.Brand>        
        </Navbar>
      )}
    </CSVReader>
    <Button variant="outline-danger" onClick={()=>dispatch(empty_data())} size="sm"  className="mt-3 mb-3"> </Button>
    
     </>
}



